import { Component, Vue, Prop } from 'vue-property-decorator';
import { ListIconsSvg } from '@/assets/svg';

@Component({
})
export default class CIconSvg extends Vue {

  @Prop([String]) public name?: string;

  constructor() {
    super();
  }

  public getContent() {
    return this.name ? ListIconsSvg[this.name] : '';
  }
}
